import React from 'react';

function PortfolioDataButton({filterItemHandeler,filter}) {
    return (
        <div className='btn_container'>
          {
              filter.map((category, index) =>{
                  return(
                      <button className='button_filter' key={index} onClick={() =>filterItemHandeler(category)}>{category}</button>
                  )
              })
          }
        </div>
    );
}

export default PortfolioDataButton;