import React,{useState} from 'react';
import DataImage from '../Components/PortfolioDat';
import Button from '../Components/PortfolioDataButton';
import PortfolioMap from '../Components/PortfolioMap';
import '../style/Portfolio.css';


const categoryAll = ['All', ...new Set (DataImage.map((Item) =>Item.category )) ];

function Portfolio() {
   const [information, setInformation] = useState(DataImage);
   const [filter, setFilter] = useState(categoryAll);

    const filterItemHandeler = (category) =>{
        if(category === 'All'){
            setInformation(DataImage);
            return;
        }
       const Filter = DataImage.filter((item) =>item.category === category);

       setInformation(Filter);
    }

    return (
        <div className='Portfolio' id='portfolio'>
            <h1>Portfolio</h1>
            <Button filterItemHandeler ={filterItemHandeler} filter={filter} />
            <PortfolioMap value = {information} />
        </div>
    );
}

export default Portfolio;