import React,{useState} from 'react';
import Footer from '../Components/Footer';
import '../style/Contact.css';

function Contact(props) {
    const [name,setName] = useState('');
    const [secondName,setSecondName] = useState('');
    const [email,setemail] = useState('');
    const [form,setForm] = useState('');

   const nameHandeler = (e) =>{
       setName(e.target.value);
   }
   const secondNameHandeler = (e) =>{
    setSecondName(e.target.value);
}
const emailHandeler = (e) =>{
    setemail(e.target.value);
}
const formHandeler = (e) =>{
    e.preventDefault();
}

    return (
        <div className='contact' id='contact'>
        <h1>Contact</h1>
           <div className='container_contact'>
           <div className='form'>
                <form onSubmit={formHandeler}>
                    <input type='text' name='name' placeholder='Name' value={name} onChange={nameHandeler} />
                    <input type='text' name='sname' placeholder='Second Name' value={secondName} onChange={secondNameHandeler} />
                    <input type='email' name='email' placeholder='Email' onChange={emailHandeler} value={email} />
                    <textarea placeholder='Comments' />
                    <input type='submit' name='Submit' onSubmit={formHandeler} />
                </form>
            </div>
            <div className='map'>
            <iframe src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d51759.335337897806!2d127.09658048312214!3d35.82549492103101!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x357023430316d863%3A0xc7bc17e091c3429a!2sJeonju-si%2C%20Jeollabuk-do!5e0!3m2!1sen!2skr!4v1619697413547!5m2!1sen!2skr" width="100%" height="100%"  allowfullscreen="" loading="lazy"></iframe>
            </div>
           </div>
           <Footer />
        </div>
    );
}

export default Contact;