import React,{useState} from 'react';
import {Link} from 'react-router-dom';
import '../style/Navbar.css';

function Navbar() {
    const [isOpen, setIsOpen] = useState(false);
    const [navbar, setNavbar] = useState(false);

    window.addEventListener('scroll',() =>{
        if (window.scrollY > 80) {
            setNavbar(true);
        }else{
            setNavbar(false);
        }
    })

    return (
        <nav className={navbar? 'nav sticky' : 'nav'} >
            <h2 className='nav_logo'><span className='pro'>Pro</span><span className='web'>web</span></h2>
            <ul className= {isOpen ? 'nav_item_mobile' : 'nav_item'}>
                <a className='nav_link active' to='/' href='#home'>
                    <li>Home</li>
                </a>
                <a className='nav_link'  href='#about'>
                    <li>About</li>
                </a>
                <a className='nav_link'  href='#skill' >
                    <li>Skills</li>
                </a>
                <a className='nav_link' href='#portfolio'>
                    <li>Portfolio</li>
                </a>
                <a className='nav_link'  href='#contact' >
                    <li>Contact</li>
                </a>
            </ul>
            <button onClick={() =>setIsOpen(!isOpen)} className='btn'>{isOpen ? <i className='fas fa-times' /> : <i className='fas fa-bars'></i> }</button>
        </nav>
    );
}

export default Navbar;