import React,{useState} from 'react';
import Data from '../Components/AboutData';
import AboutCard from './AboutCard';

function AboutReady() {

    const [aboutdata, setAboutData] = useState(Data);
    return (
        <div>
            {
                aboutdata.map((item) =>{
                    return(
                        <AboutCard image={item.image} title={item.title} subTitle= {item.subTitel} desc={item.desc} key={item.id} />
                    )
                })
            }
        </div>
    );
}

export default AboutReady;