import React from 'react';
import '../style/AboutCard.css';


function AboutCard(props) {
    return (
        <div className='card'>
            <div className='flex-1 grid'>
                <div className='card_img'>
                <img src={props.image} />
                </div>
            </div>
            <div className='flex-1 grid-1'>
                <div className='info'>
                <h2>{props.title}</h2>
                <p>{props.subTitle}</p>
                <p className='ptag'>{props.desc}</p>
                <br />
                <a href='#' className='about_btn'>download CV</a>
                </div>
            </div>
        </div>
    );
}

export default AboutCard;