import React from 'react';
import '../style/SkillBar.css';

function SkillBar(props) {
    return (
        <div className='skill_bar'>
            <div className='bar_data'>
                <h2>HTML</h2>
                <p>90%</p>
                <div className='bar_level'></div>
            </div>
            <div className='bar_data'>
                <h2>CSS</h2>
                <p>90%</p>
                <div className='bar_level'></div>
            </div>
            <div className='bar_data'>
                <h2>JavaScript/jQuery</h2>
                <p>70%</p>
                <div className='bar_level react'></div>
            </div>
            <div className='bar_data'>
                <h2>React</h2>
                <p>70%</p>
                <div className='bar_level react'></div>
            </div>
        </div>
    );
}

export default SkillBar;